.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: column;
      flex-direction: column;
  padding-bottom: 10px;
  width: -webkit-fill-available;
}
.input-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  padding: 15px;
  background: url(../../img/mainPage.png) no-repeat;
  background-size: 200%;
  background-position-y: center;
}
.text-field {
  margin-top: 0;
  width: 100%;
}
.menu {
  width: 100%;
}
.login-button {
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  width: 200px;
  color: #fff !important;
  background-color: #2196f3 !important;
}
.login-header {
  padding: 15px;
}

.login-button:hover {
  background-color: #1b77c5 !important;
}

.sign-up-button {
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  text-decoration: none;
  color: #2196f3;
}

.sign-up-button:hover {
  color: #000;
}

@media (min-width: 800px) {
  .container {
      width: 100%;
  }
  .input-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color: 'red';
    background-size: 100%;
  }

  .input-panels {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .text-field {
    -ms-flex-positive: 2;
        flex-grow: 2;
    margin-top: 0;
    width: 300px;
  }

  .login-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .login-button {
    width: 300px;
  }

  .text-panel {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-positive: 1;
        flex-grow: 1;
  }
}




